	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import xueshengtuike from '@/views/xueshengtuike/list'
	import news from '@/views/news/list'
	import xueshengxuanke from '@/views/xueshengxuanke/list'
	import xuesheng from '@/views/xuesheng/list'
	import chengjipingjia from '@/views/chengjipingjia/list'
	import xuexiqushi from '@/views/xuexiqushi/list'
	import baogaopingjia from '@/views/baogaopingjia/list'
	import jiaoshi from '@/views/jiaoshi/list'
	import xueshengchengji from '@/views/xueshengchengji/list'
	import storeup from '@/views/storeup/list'
	import kechengxinxi from '@/views/kechengxinxi/list'
	import users from '@/views/users/list'
	import zhuanye from '@/views/zhuanye/list'
	import jiazhang from '@/views/jiazhang/list'
	import banzhuren from '@/views/banzhuren/list'
	import chengjixinxi from '@/views/chengjixinxi/list'
	import xuexibaogao from '@/views/xuexibaogao/list'
	import banji from '@/views/banji/list'
	import config from '@/views/config/list'
	import jiaoshiRegister from '@/views/jiaoshi/register'
	import jiaoshiCenter from '@/views/jiaoshi/center'
	import banzhurenRegister from '@/views/banzhuren/register'
	import banzhurenCenter from '@/views/banzhuren/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/jiaoshiCenter',
			name: '教师个人中心',
			component: jiaoshiCenter
		}
		,{
			path: '/banzhurenCenter',
			name: '班主任个人中心',
			component: banzhurenCenter
		}
		,{
			path: '/xueshengtuike',
			name: '学生退课',
			component: xueshengtuike
		}
		,{
			path: '/news',
			name: '公告信息',
			component: news
		}
		,{
			path: '/xueshengxuanke',
			name: '学生选课',
			component: xueshengxuanke
		}
		,{
			path: '/xuesheng',
			name: '学生',
			component: xuesheng
		}
		,{
			path: '/chengjipingjia',
			name: '成绩评价',
			component: chengjipingjia
		}
		,{
			path: '/xuexiqushi',
			name: '学习趋势',
			component: xuexiqushi
		}
		,{
			path: '/baogaopingjia',
			name: '报告评价',
			component: baogaopingjia
		}
		,{
			path: '/jiaoshi',
			name: '教师',
			component: jiaoshi
		}
		,{
			path: '/xueshengchengji',
			name: '学生成绩',
			component: xueshengchengji
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/kechengxinxi',
			name: '课程信息',
			component: kechengxinxi
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/zhuanye',
			name: '专业',
			component: zhuanye
		}
		,{
			path: '/jiazhang',
			name: '家长',
			component: jiazhang
		}
		,{
			path: '/banzhuren',
			name: '班主任',
			component: banzhuren
		}
		,{
			path: '/chengjixinxi',
			name: '成绩信息',
			component: chengjixinxi
		}
		,{
			path: '/xuexibaogao',
			name: '学习报告',
			component: xuexibaogao
		}
		,{
			path: '/banji',
			name: '班级',
			component: banji
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		]
	},
	{
		path: '/jiaoshiRegister',
		name: '教师注册',
		component: jiaoshiRegister
	},
	{
		path: '/banzhurenRegister',
		name: '班主任注册',
		component: banzhurenRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
